<!--  -->
<template>
  <div class="event">
       <div class="huHome_header">
            赛事专区
        </div>
        <ul class="Event_ul">
            <li @click="tournameJump(item.is_delete,item.u_like)" v-for="(item,index) in TournamentAreaData" :key="index">
                    <img :src="item.logo" alt="">
            </li>
        </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
        TournamentAreaData:'',
    };
  },

  components: {},

  computed: {},

  mounted() {
      //   赛事专区数据
                this.getTournamentArea()
  },

  methods: {
        //  赛事专区数据
                getTournamentArea(){
                    this.$http.post('/api/home/index').then((res)=>{
                        this.TournamentAreaData=res.data.data.result
                    })
                },
        // 点击赛事专区列表跳转
                tournameJump(is_delete,u_like){
                    if(is_delete==1){
                        window.location.href=u_like
                    }
                },
  }
}

</script>
<style scoped lang="stylus">
.huHome_header{
    width:100%;
    height:1.23rem;
    text-align :center;
    line-height :1.23rem;
    font-size:0.48rem;
    border-bottom:0.03rem solid rgba(0, 0, 0, 0.1);
}
.Event_ul{
    display :flex;
    flex-direction :column;
    align-items :center;
    padding:0.32rem 0;
    background :#fff;
    >li{
        width:9.2rem;
        height:5.07rem;
        margin-bottom:0.27rem;
        background :pink;
        >img{
            width:9.2rem;
            height:5.07rem;
        }
    }
}
.event{
    width:100%;
    height:100%;
    background :#fff;
}
</style>


